function render() {
    var html = "<div>";
    var mmToInch = 1 / 25.4;
    var printableWidthInMM = 200;
    var printableHeightInMM = 279;
    var dpi = 300;
    var xPixels = Math.ceil(dpi * printableWidthInMM * mmToInch);
    var yPixels = Math.floor(dpi * printableHeightInMM * mmToInch);
    html += "<h1 class=\"non-printable\">Math Generator</h1>";
    var svgProblems = "";
    var svgSolutions = "";
    svgProblems = "<svg class=\"non-selectable\" style=\"break-after:page\" viewBox=\"0 0 ".concat(xPixels, " ").concat(yPixels, "\" width=\"").concat(printableWidthInMM, "mm\" height=\"").concat(printableHeightInMM, "mm\" xmlns=\"http://www.w3.org/2000/svg\">");
    svgSolutions = "<svg class=\"non-selectable\" style=\"break-after:page\" viewBox=\"0 0 ".concat(xPixels, " ").concat(yPixels, "\" width=\"").concat(printableWidthInMM, "mm\" height=\"").concat(printableHeightInMM, "mm\" xmlns=\"http://www.w3.org/2000/svg\">");
    var textSize = Math.ceil(0.125 * dpi);
    var gridWidth = Math.ceil(0.15 * dpi);
    var gridHeight = Math.ceil(0.25 * dpi);
    var charXOffset = Math.ceil(0.0275 * dpi);
    var charYOffset = Math.round(-0.02 * dpi);
    svgProblems += "\n    <style>\n        .digits {\n            font-family: Consolas, monaco, monospace;\n            font-size: ".concat(textSize, "px;\n            font-style: normal;\n            font-variant: normal;\n            font-weight: 700;\n            line-height: ").concat(textSize, "px;\n        }\n        .carryover {\n            font-family: Consolas, monaco, monospace;\n            font-size: ").concat(textSize / 2, "px;\n            font-style: normal;\n            font-variant: normal;\n            font-weight: 700;\n            line-height: ").concat(textSize / 2, "px;\n        }\n    </style>");
    svgSolutions += "\n    <style>\n        .digits {\n            font-family: Consolas, monaco, monospace;\n            font-size: ".concat(textSize, "px;\n            font-style: normal;\n            font-variant: normal;\n            font-weight: 700;\n            line-height: ").concat(textSize, "px;\n        }\n        .carryover {\n            font-family: Consolas, monaco, monospace;\n            font-size: ").concat(textSize / 2, "px;\n            font-style: normal;\n            font-variant: normal;\n            font-weight: 700;\n            line-height: ").concat(textSize / 2, "px;\n        }\n    </style>");
    var cornerSize = Math.ceil(0.05 * dpi);
    // svgProblems += `<rect width="${cornerSize}" height="${cornerSize}" x="0" y="0" fill="black" />`;
    // svgProblems += `<rect width="${cornerSize}" height="${cornerSize}" x="${xPixels - cornerSize}" y="0" fill="black" />`;
    // svgProblems += `<rect width="${cornerSize}" height="${cornerSize}" x="0" y="${yPixels - cornerSize}" fill="black" />`;
    // svgProblems += `<rect width="${cornerSize}" height="${cornerSize}" x="${xPixels - cornerSize}" y="${yPixels - cornerSize}" fill="black" />`;
    // svgSolutions += `<rect width="${cornerSize}" height="${cornerSize}" x="0" y="0" fill="black" />`;
    // svgSolutions += `<rect width="${cornerSize}" height="${cornerSize}" x="${xPixels - cornerSize}" y="0" fill="black" />`;
    // svgSolutions += `<rect width="${cornerSize}" height="${cornerSize}" x="0" y="${yPixels - cornerSize}" fill="black" />`;
    // svgSolutions += `<rect width="${cornerSize}" height="${cornerSize}" x="${xPixels - cornerSize}" y="${yPixels - cornerSize}" fill="black" />`;
    function character(char, x, y) {
        return "<text x=\"".concat(cornerSize + x * gridWidth + charXOffset, "\" y=\"").concat(cornerSize + textSize + gridHeight * y + charYOffset, "\" class=\"digits\">").concat(char, "</text>");
    }
    function carryover(char, x, y) {
        return "<text x=\"".concat(cornerSize + x * gridWidth + charXOffset + 5, "\" y=\"").concat(cornerSize + gridHeight * y + charYOffset, "\" class=\"carryover\">").concat(char, "</text>");
    }
    function equationLine(left, right, y) {
        return "<rect x=\"".concat(cornerSize + left * gridWidth, "\" y=\"").concat(cornerSize + textSize + gridHeight * y, "\" width=\"").concat(Math.ceil((right - left + 1) * gridWidth), "\" height=\"").concat(Math.ceil(0.01 * dpi), "\" fill=\"black\" />");
    }
    function blanks(left, right, y, color) {
        if (color === void 0) { color = "black"; }
        var svg = "";
        for (var x = left; x <= right; x++) {
            svg += "<rect x=\"".concat(cornerSize + x * gridWidth + Math.floor(gridWidth * 0.15), "\" y=\"").concat(cornerSize + textSize + gridHeight * y - Math.floor(gridHeight * 0.1), "\" width=\"").concat(Math.ceil(gridWidth * 0.7), "\" height=\"").concat(Math.ceil(0.01 * dpi), "\" fill=\"lightgray\" />");
        }
        return svg;
    }
    for (var column = 0; column < 3; column++) {
        for (var row = 0; row < 6; row++) {
            var columnChars = 17;
            var problemRowLines = 7;
            var problemRowOffset = 2;
            var problemColumnOffset = -2;
            var firstNumberLength = random(3, 6);
            var secondNumberLength = random(2, Math.min(4, firstNumberLength - 1));
            var numberA = random(Math.pow(10, firstNumberLength - 1), Math.pow(10, firstNumberLength) - 1);
            var numberB = random(Math.pow(10, secondNumberLength - 1), Math.pow(10, secondNumberLength) - 1);
            var top_1 = row * problemRowLines + problemRowOffset;
            var left = column * columnChars + problemColumnOffset;
            var problem = numberA.toString() + "Â·" + numberB.toString();
            for (var char = 0; char < problem.length; char++) {
                var text = character(problem[char], (columnChars + 1 - problem.length) + left + char, top_1);
                svgProblems += text;
                svgSolutions += text;
            }
            svgProblems += character("+", left + columnChars - problem.length + 1, top_1 + 0.5 * secondNumberLength + 0.1);
            svgSolutions += character("+", left + columnChars - problem.length + 1, top_1 + 0.5 * secondNumberLength + 0.1);
            svgProblems += equationLine(left + columnChars - problem.length + 1, left + columnChars, top_1);
            svgSolutions += equationLine(left + columnChars - problem.length + 1, left + columnChars, top_1);
            for (var eq = 0; eq < secondNumberLength; eq++) {
                svgProblems += blanks(left + columnChars - firstNumberLength - eq, left + columnChars - eq, top_1 + eq + 1);
                svgSolutions += blanks(left + columnChars - firstNumberLength - eq, left + columnChars - eq, top_1 + eq + 1);
            }
            svgProblems += equationLine(left + columnChars - problem.length + 1, left + columnChars, top_1 + secondNumberLength);
            svgSolutions += equationLine(left + columnChars - problem.length + 1, left + columnChars, top_1 + secondNumberLength);
            svgProblems += blanks(left + columnChars - firstNumberLength - secondNumberLength + 1, left + columnChars, top_1 + secondNumberLength + 1);
            svgSolutions += blanks(left + columnChars - firstNumberLength - secondNumberLength + 1, left + columnChars, top_1 + secondNumberLength + 1);
            var lineNumbers = [];
            for (var row_1 = 0; row_1 < secondNumberLength; row_1++) {
                lineNumbers.push([]);
                for (var column_1 = 0; column_1 < firstNumberLength + secondNumberLength; column_1++) {
                    lineNumbers[row_1][column_1] = 0;
                }
            }
            // Solve...
            for (var solutionLine = 0; solutionLine < secondNumberLength; solutionLine++) {
                var digitB = Number.parseInt(numberB.toString()[numberB.toString().length - 1 - solutionLine]);
                if (digitB == 0) {
                    svgSolutions += character("0", left + columnChars - solutionLine, top_1 + 1 + solutionLine);
                    continue;
                }
                var carryOver = 0;
                for (var i = 0; i < firstNumberLength; i++) {
                    var digitA = Number.parseInt(numberA.toString()[numberA.toString().length - 1 - i]);
                    var result = carryOver + digitA * digitB;
                    var resultDigit = result % 10;
                    carryOver = Math.floor(result / 10);
                    lineNumbers[solutionLine][firstNumberLength + secondNumberLength - 1 - i - solutionLine] = resultDigit;
                    svgSolutions += character(resultDigit.toString(), left + columnChars - i - solutionLine, top_1 + 1 + solutionLine);
                    if (carryOver > 0) {
                        svgSolutions += carryover(carryOver.toString(), left + columnChars - i - solutionLine - 1, top_1 + 1 + solutionLine);
                    }
                }
                if (carryOver > 0) {
                    svgSolutions += character(carryOver.toString(), left + columnChars - firstNumberLength - solutionLine, top_1 + 1 + solutionLine);
                    lineNumbers[solutionLine][firstNumberLength + secondNumberLength - 1 - firstNumberLength - solutionLine] = carryOver;
                }
            }
            // TODO: Calculate the lineNumbers[row][col] to figure out carryovers
            var carryoverTotal = 0;
            for (var i = 0; i < firstNumberLength + secondNumberLength; i++) {
                var sum = carryoverTotal;
                for (var j = 0; j < secondNumberLength; j++) {
                    sum += lineNumbers[j][firstNumberLength + secondNumberLength - 1 - i];
                }
                var solutionDigit = sum % 10;
                carryoverTotal = Math.floor(sum / 10);
                if (i < firstNumberLength + secondNumberLength - 1 || solutionDigit != 0) {
                    svgSolutions += character(solutionDigit.toString(), left + columnChars - i, top_1 + 1 + secondNumberLength);
                }
                if (carryoverTotal > 0) {
                    svgSolutions += carryover(carryoverTotal.toString(), left + columnChars - i - 1, top_1 + 1 + secondNumberLength);
                }
            }
            // const solution = (numberA * numberB).toString();
            // for (let char = 0; char < solution.length; char++) {
            //     svgSolutions += character(solution[char], left + columnChars - solution.length + 1 + char, top + 1 + secondNumberLength);
            // }
        }
    }
    svgProblems += "</svg>";
    svgSolutions += "</svg>";
    html += svgProblems;
    html += svgSolutions;
    html += "</div>";
    var parser = new DOMParser();
    var htmlDoc = parser.parseFromString(html, 'text/html');
    return htmlDoc.querySelector("div");
}
function random(from, to) {
    return from + Math.floor(Math.random() * (to - from + 1));
}
document.body.appendChild(render());
